<template>
  <div class="home d-flex justify-content-center align-items-center position-relative">
    <div class="game-rule-bg position-absolute d-flex justify-content-center align-items-center">
      <div class="px-4">
        <img src="../assets/img/list_up.png" width="100%" alt="">
        <div class="bg-white p-4 game-rule mx-auto mt-2 mb-3">
          <div class="game-list-title text-center py-1 mt-1">戳戳樂獎項</div>
          <div class="list-content">
            <div class="text-center d-flex">
              <div class="game-list-icon d-flex justify-content-center align-items-center py-1">
                <img src="../assets/img/u_point.png" height="30px" alt="">
              </div>
              <div class="game-list-text d-flex text-center justify-content-center align-items-center">
                <p class="h4 text-main mb-0 font-weight-bold">UUPON點數2~27點</p>
              </div>
            </div>
            <div class="text-center d-flex">
              <div class="game-list-icon d-flex justify-content-center align-items-center py-1">
                <img src="../assets/img/heart.png" height="30px" alt="">
              </div>
              <div class="game-list-text d-flex text-center justify-content-center align-items-center">
                <p class="h4 text-main mb-0 font-weight-bold">愛心</p>
              </div>
            </div>
            <div class="text-center d-flex">
              <div class="game-list-icon game-list-last-left d-flex justify-content-center align-items-center py-1">
                <img src="../assets/img/patronage.png" height="28px" alt="">
              </div>
              <div class="game-list-text d-flex text-center game-list-last-right justify-content-center align-items-center">
                <p class="h4 text-main mb-0 font-weight-bold">銘謝惠顧</p>
              </div>
            </div>
          </div>
        </div>
        <img src="../assets/img/down.png" width="100%" alt="">
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'home',
  data () {
    return {
      moreShow: false
    }
  },
  mounted () {
    this.$nextTick(() => {
      document.querySelector('.home').scrollTop = 0
    })
  },
  methods: {
    goToGame () {
      // eslint-disable-next-line
      gtag('event', 'Page Click', { 
        'event_category': 'Click',
        'event_label': '進入遊戲按鈕',
        'value': 1
      })
      this.$router.push({ name: 'Game' })
    },
    moreDetail () {
      // eslint-disable-next-line
      gtag('event', 'Page Click', { 
        'event_category': 'Click',
        'event_label': 'btn_點我看更多注意事項',
        'value': 1
      })
      this.moreShow = !this.moreShow
      document.querySelector('.triangle').style.transform = this.moreShow ? 'rotate(180deg)' : 'rotate(0deg)'
    }
  }
}
</script>

<style>
  .home{
    background: url('../assets/img/bg.png') no-repeat center  bottom;
    width: 100%;
    height: 100vh;
    -webkit-background-size: cover ;
    background-size: cover ;
    background-attachment: fixed;
  }
  .introduce-text{
    line-height: 1.28;
    font-family: CenturyGothic;
    text-align: center;
    color: #834141;
  }
  .title-btn{
    transform: translate(93%, -160%) scale(0.9);
  }
  .home .close-btn{
    top: -2%;
    right: -2%;
  }
  @keyframes down{
    0%{
      transform: translateY(-100%);
    }
    50%{
      transform: translateY(-50%);
    }
    100%{
      transform: translateY(0%);
    }
  }
</style>



