<template>
    <div class="game-rule-bg position-absolute d-flex justify-content-center align-items-center">
      <div class="px-4 game-rule-content d-flex align-items-center">
        <div class="mx-90">
          <img src="../assets/img/rule_up.png" width="100%" alt="">
          <div class="bg-white p-4 game-rule mx-auto mt-2 mb-3">
            <p class="h5 text-main mb-2 font-weight-bold"><b>活動說明 :</b></p>
            <div class="text-third">
              <p class="h4"><b>1. 於活動期間內，點選「遊戲開始」，將出現一張6*6的戳戳樂。</b></p>
              <p class="h4"><b>2. 選擇一格您所喜愛的格子並點下，系統將扣除5點UUPON點數或15顆愛心 (若點數不足或愛心不足者，將無法進行遊戲)。</b></p>
              <p class="h4"><b>3. 最後看看所找尋到的是什麼神秘寶藏吧！</b></p>
            </div>
            <p class="h5 text-purple font-weight-bold">使用點數: 5點或15顆愛心</p>
            <p class="h5 text-main mb-2 font-weight-bold">活動時間：即日起至2021/05/31</p>
            <p class="h5 text-main"><b>驚喜Bonus :</b></p>
            <p class="h4 text-third"><b>戳中的愛心收集滿15顆，就能免費再戳一格哦！</b></p>
            <p class="h5 text-main"><b>獎項公告 :</b></p>
            <p class="h4 text-third"><b>抽中之點數、優惠券商品序號，將於得獎紀錄中提供</b></p>
          </div>
          <div class="position-relative z-999 d-flex align-items-center justify-content-center">
            <div class="more-rule mb-0 position-absolute py-1 text-light d-flex justify-content-center align-items-center h5"  @click="moreDetail">
              <b>點我看更多注意事項</b>
              <span class="triangle ml-1 mt-1"></span>
            </div>
            <img src="../assets/img/down.png" class="down-img" width="100%" alt="">
          </div>
          <div id="more" class="more-detail px-3 pb-5" >
            <transition name="slide-fade">
              <div class="w-100 bg-white p-4 game-rule-more mx-auto height-auto h3" v-show="moreShow">
                <p>1. 每次活動將扣除5點UUPON點數或15顆愛心 (若點數不足或愛心不足者，將無法進行遊戲)。</p>
                <p>2. 確認欲參與遊戲後，遊戲點數或愛心將會即時扣除，無法取消或退還。</p>
                <p>3. 如欲新開一局，將依照目前該局所戳之格數進行扣點，方可更換新局，相關規則如下：</p>
                <p class="text-danger h2">*優惠通知：新遊戲上路，優惠期間新開一局將不進行扣點(戳一格後方可免費開新局)，優惠結束後將依照遊戲規則扣除點數*</p>
                <p>4. 遊戲之抽獎獎品將依活動實際公告為主。</p>
                <p>5.  若有實體獎項，獎品寄送地點限中華民國境內，無法配合領獎事宜者，將視同自動放棄中獎資格，不得異議。</p>
                <p>6. 抽中之優惠券商品序號及使用效期，將於得獎紀錄中提供 (請至獎品記錄查詢，並留意使用效期)。</p>
                <p>7. 點鑽整合行銷股份有限公司保留審核、決定及變更獎項內容之權利，如因不可抗拒之因素，點鑽整合行銷股份有限公司有權更換獎項為等值商品。</p>
                <p>8. 點鑽整合行銷股份有限公司保留隨時修正、暫停或終止本活動之權利，如有變動並將另行公告於活動頁面。</p>
              </div>
            </transition>
          </div>
        </div>
      </div>
    </div>
</template>

<script>
export default {
  name: 'home',
  data () {
    return {
      moreShow: false
    }
  },
  mounted () {
    this.$nextTick(() => {
      document.querySelector('.game-rule-bg').scrollTop = 0
    })
  },
  methods: {
    goToGame () {
      // eslint-disable-next-line
      gtag('event', 'Page Click', { 
        'event_category': 'Click',
        'event_label': '進入遊戲按鈕',
        'value': 1
      })
      this.$router.push({ name: 'Game' })
    },
    moreDetail () {
      // eslint-disable-next-line
      gtag('event', 'Page Click', { 
        'event_category': 'Click',
        'event_label': 'btn_點我看更多注意事項',
        'value': 1
      })
      this.moreShow = !this.moreShow
      document.querySelector('.triangle').style.transform = this.moreShow ? 'rotate(180deg)' : 'rotate(0deg)'
      this.$nextTick(() => {
        document.querySelector('.game-rule-content').scrollTop = 50
      })
    }
  }
}
</script>

<style>
  .home{
    background: url('../assets/img/bg.png') no-repeat center  bottom;
    width: 100%;
    height: 100vh;
    -webkit-background-size: cover ;
    background-size: cover ;
    background-attachment: fixed;
  }
  .introduce-text{
    line-height: 1.28;
    font-family: CenturyGothic;
    text-align: center;
    color: #834141;
  }
  .title-btn{
    transform: translate(93%, -160%) scale(0.9);
  }
  .rule-title{
    border-radius: 30px;
    background-color: #5ab0cb;
    font-family: DFHeiStd-W9;
    /* font-size: 48px; */
    font-weight: normal;
    font-stretch: normal;
    font-style: normal;
    line-height: 0.88;
    letter-spacing: 1.2px;
    text-align: center;
    color: white;
    width: fit-content;
  }
  .triangle{
    width: 0;
    height: 0;
    border-style: solid;
    border-width: 12px 8.5px 0 8.5px;
    border-color: #ffffff transparent transparent transparent;
  }

  .home .close-btn{
    top: -2%;
    right: -2%;
  }
  @keyframes down{
    0%{
      transform: translateY(-100%);
    }
    50%{
      transform: translateY(-50%);
    }
    100%{
      transform: translateY(0%);
    }
  }
  .more-detail{
    /* overflow: hidden; */
    /* margin-top: -12px; */
  }
</style>



