<template>
  <div class="home d-flex justify-content-center align-items-center position-relative">
    <div class="px-4 text-center">
      <img src="../assets/img/title.png" width="100%" alt="">
      <div class="my-3 introduce-text font-weight-bold h6">
        <p>在5月春暖花開之際</p>
        <p>戀愛運勢也變得特別好!</p>
        <p>想跟心儀對象告白了嗎?</p>
        <p>先來玩個遊戲放鬆心情吧!</p>
      </div>
      <img src="../assets/img/enter_btn.png" width="50%" alt="" @click="goToGame">
      <div class="d-flex justify-content-center mt-4">
        <img src="../assets/img/rule_btn.png" class="mr-4" width="100px" height="100px" alt="" @click="ruleShow">
        <img src="../assets/img/list_btn.png" width="100px" height="100px" alt="" @click="listShow">
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'home',
  data () {
    return {
      moreShow: false
    }
  },
  mounted () {
    this.searchLast()
    this.$nextTick(() => {
      document.querySelector('.home').scrollTop = 0
    })
  },
  methods: {
    goToGame () {
      // eslint-disable-next-line
      gtag('event', 'Page Click', { 
        'event_category': 'Click',
        'event_label': '進入遊戲按鈕',
        'value': 1
      })
      this.$router.push({ name: 'Game' })
    },
    ruleShow () {
      // eslint-disable-next-line
      gtag('event', 'Page Click', { 
        'event_category': 'Click',
        'event_label': 'modal_遊戲規則頁',
        'value': 1
      })
      this.$router.push({name: 'Rule'})
    },
    listShow () {
      // eslint-disable-next-line
      gtag('event', 'Page Click', { 
        'event_category': 'Click',
        'event_label': 'modal_寶藏清單頁面',
        'value': 1
      })
      this.$router.push({name: 'List'})
    },
    moreDetail () {
      // eslint-disable-next-line
      gtag('event', 'Page Click', { 
        'event_category': 'Click',
        'event_label': 'btn_點我看更多注意事項',
        'value': 1
      })
      this.moreShow = !this.moreShow
      document.querySelector('.triangle').style.transform = this.moreShow ? 'rotate(180deg)' : 'rotate(0deg)'
    }
  }
}
</script>

<style>
  .home{
    background: url('../assets/img/bg.png') no-repeat center  bottom;
    width: 100%;
    height: 100vh;
    -webkit-background-size: cover ;
    background-size: cover ;
    background-attachment: fixed;
  }
  .introduce-text{
    line-height: 1.28;
    font-family: CenturyGothic;
    text-align: center;
    color: #834141;
  }
  .title-btn{
    transform: translate(93%, -160%) scale(0.9);
  }

  .home .close-btn{
    top: -2%;
    right: -2%;
  }
  @keyframes down{
    0%{
      transform: translateY(-100%);
    }
    50%{
      transform: translateY(-50%);
    }
    100%{
      transform: translateY(0%);
    }
  }
</style>



