<template>
  <div class="record-bg position-absolute d-flex justify-content-center align-items-center">
    <div class="px-4">
      <img src="../assets/img/record_up.png" width="100%" alt="">
      <div class="bg-white mx-auto my-3 p-4 game-rule position-relative">
        <div class="record-content">
          <p class="h5 text-center text-green-light mb-0"><b>獎品記錄</b></p>
          <div class="table-frame mt-2">
            <table class="table table-bordered table-striped font-weight-bold mb-0">
              <thead>
                <tr>
                  <th class="text-white font-weight-bold text-center text-nowrap">獎項 x 數量</th>
                </tr>
              </thead>
              <tbody class="">
                <tr v-for="(items, index) in recordList" :key="index">
                  <td class="font-weight-bold h4 text-break">{{ items.item }}{{ items.item === 'UUPON 點數'? `${items.qty}點`:'' }}{{ items.item === '愛心'? `${items.qty}顆`:''}}
                    <div v-if="items.note !== ''" class="mt-1" >
                      {{ items.note }}
                      <p @click="showList(index)" class="text-green-light mt-1 mb-0 h2"><u>優惠券使用說明</u></p>
                      <transition name="fade">
                        <div v-if="listNum === index">
                          <!-- <div v-if="items.item.includes('阿瘦')" class="h2 p-2 text-left mb-0">
                            <p>憑券來阿瘦門市送健康襪乙雙，並免費體驗「動態足壓量測」。</p>
                            <p>阿瘦皮鞋推出全國首創「動態足壓量測」系統，結合工研院專利技術與物理治療師的專業判讀，分析4大足健康指標，打造個人專屬足部保養對策。</p>
                            <br>
                            <p>取貨期限：2021.03.21 23:59:59 止</p>
                            <p>取貨店家：阿瘦皮鞋</p>
                          </div> -->
                        </div>
                      </transition>
                    </div>
                  </td>
                </tr>
              </tbody>
            </table>
          </div>
          <div class="mt-1 d-flex justify-content-center align-items-center" v-if="totalPage !== 0">
            <span class="h5 text-center text-green-light" v-if="currentPage !== 1"  @click="currentPage--">上一頁</span>
            <span class="h5 text-center text-green-light mx-1"> {{ currentPage }} </span>
            <span class="h5 text-center text-green-light" v-if="currentPage !== totalPage" @click="currentPage++">下一頁</span>
            <span class="h5 text-center text-green-light ml-1">每頁 {{ pageSize }} 筆</span>
          </div>
        </div>
      </div>
      <div class="position-relative z-999 d-flex align-items-center justify-content-center">
        <div class="more-rule w-50 mb-0 position-absolute py-1 text-light d-flex justify-content-center align-items-center h5"  @click="$router.replace({name: 'Game'})">
          <b>返回遊戲</b>
        </div>
        <img src="../assets/img/down.png" class="down-img" width="100%" alt="">
      </div>
    </div>
    <!-- 錯誤顯示 -->
    <div v-if="errorModal" class="notice-bg position-absolute d-flex align-items-center">
      <div v-if="errorModal" class="bg-white notice m-auto w-75 position-relative p-4">
        <div>
          <p class="h6 mb-1 text-second text-center"><b>{{ errorMsg }}</b></p>
          <div class="text-center mt-3 d-flex justify-content-center">
            <p class="notice-btn-agree text-white py-2 h6 mb-0" @click="errorModal = false"><b>確認</b></p>
          </div>
        </div>
      </div>
    </div>
    <transition name="fade">
      <loading v-if="isLoading"></loading>
    </transition>
  </div>
</template>

<script>
import $ from 'jquery'
import Loading from '../components/Loading'
const cors = '' // 解決跨域問題 上測試記得拿掉https://cors-anywhere.herokuapp.com/
export default {
  name: 'record',
  components: {
    Loading
  },
  data () {
    return {
      isLoading: true,
      recordList: [],
      totalPage: 1,
      currentPage: 1,
      boxToken: '',
      pageSize: 0,
      errorModal: false,
      errorMsg: '',
      listNum: ''
    }
  },
  created () {
    this.boxToken = this.$route.params.token
  },
  mounted () {
    this.getRecordList()
    this.$nextTick(() => {
      document.querySelector('.record-bg').scrollTop = 0
    })
  },
  watch: {
    currentPage () {
      // eslint-disable-next-line
      gtag('event', 'Page Click', { 
        'event_category': 'Click',
        'event_label': `btn_切換頁數`,
        'value': 1
      })
      this.getRecordList()
    }
  },
  methods: {
    getRecordList () {
      const vh = this
      vh.isLoading = true
      $.ajax({
        type: 'POST',
        url: `${cors}/luckydraw/api/rewardLog`,
        // processData: false, // Important!
        // contentType: false,
        cache: false,
        /*
        // 跨域請求 crossDomain: true,
        */
        // crossDomain: true,
        xhrFields: {
          withCredentials: true // 跨域請求是否附帶 Cookies/HTTP Auth/Client SSL, xhr.withCredentials = true, Server 設定 Access-Control-Allow-Origin
        },
        data: {
          // kn: '0886000000000000009575', // 測試用 要拿掉
          boxToken: vh.boxToken,
          page: vh.currentPage
        },
        timeout: 600000,
        dataType: 'json',
        success: function (response) {
          if (!response.error) {
            vh.recordList = response.data.records
            vh.totalPage = response.data.recordPages
            vh.pageSize = response.data.pageSize
            document.querySelector('.table-frame').scrollTop = 0
          } else {
            // alert('發生錯誤：' + response.error.msg)
            vh.errorMsg = response.error.msg
            vh.errorModal = true
            // $("#btn-new-game").attr('disabled', false);
          }
          vh.isLoading = false
        },
        error: function (jqXHR) {
          // alert('發生錯誤：' + jqXHR.status)
          // $("#btn-new-game").attr('disabled', false)
        }
      })
    },
    showList (index) {
      this.listNum === index ? this.listNum = '' : this.listNum = index
    }
  }
}
</script>

<style>
  .record-bg{
    background: url('../assets/img/bg.png') no-repeat center  bottom;
    width: 100%;
    height: 100vh;
    -webkit-background-size: cover ;
    background-size: cover ;
    background-attachment: fixed;
  }
  .table-frame{
    height: 300px;
    overflow: scroll;
    border-radius: 0 0 15px 15px;
  }
  .table-bordered td {
    border: 1px solid #ffffff !important;
  }
  tr:nth-of-type(odd) td{
    background-color: #fff0e4!important;
  }
  tr:nth-of-type(even) td{
    background-color: #fae9db!important;
  }
  tr:last-child td:first-child { border-bottom-left-radius: 15px; }
  tr:last-child td:last-child { border-bottom-right-radius: 15px; }
  .table{
    color: #a41654;
    font-weight: 900;
  }
  table tbody{
    max-height: 300px;
    overflow: scroll;
  }
  /* thead{
    background: #a4b329;
    text-align: center;
  } */
  .table-bordered, .table-bordered td, .table-bordered th {
      border: 1px solid white !important;
  }
  .table td {
  text-align: center;
  }
  .table-bordered {
    border: none;
  }
  table thead th {
    border: none;
  }
  .table-bordered thead th:nth-of-type(1){
    border-radius: 15px 15px 0 0;
  }
  .table-bordered thead th{
    background: #d9105b;
  }
  /* 下拉樣式 */
  .fade-enter-active, .fade-leave-active {
  transition: opacity .3s;
  }
  .fade-enter, .fade-leave-to /* .fade-leave-active below version 2.1.8 */ {
    opacity: 0;
  }
</style>



